* {
  padding: 0;
  margin: 0;
  width: 100%;
  height: auto;
  font-family: "Dosis", sans-serif;
  overflow-x: hidden;
  box-sizing: inherit;
  transition-property: all;
  transition-duration: 0.6s;
  transition-timing-function: ease;
  scroll-behavior: smooth;
}

html,
body {
  box-sizing: border-box;
  height: 100%;
}
