.footer {
  color: white;
  background-color: #9A544C;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 45px;
  padding-bottom: 10px;
  text-align: center;
  position: relative;
  bottom: 0;

  .fa {
    color: white;
    font-size: 35px !important;
    margin: 10px;
  }

  .credits {
    align-items: center;
    justify-content: center;
    display: flex;
    font-size: 15px;

    .copyright {
      overflow: hidden;
    }
  }
}
