#sponsors {
  background-color: #9a544c;
  color: white;
  scroll-snap-align: start;

  img {
    display: block;
  }

  h2 {
    text-align: center;
    font-size: 3rem;
    text-transform: uppercase;
    margin: 4% 0;
    font-weight: 900;
    overflow: hidden;
  }

  #sponsors-grid {
    padding: 0 2%;
    overflow: hidden;
    justify-content: center;
  }

  #platinum {
    grid-area: platinum;
    display: grid;
    grid-template-areas: ". asucm mlh .";
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: auto;
    grid-gap: 6%;
    margin-bottom: 1%;
  }

  #silver {
    grid-area: silver;
    display: grid;
    grid-template-areas:
      " Founders Linode LLNL"
      "DigitalOcean . Roche ";
  }

  #bronze {
    grid-area: bronze;
    display: grid;
    justify-content: center;
    grid-auto-flow: row;
    overflow: hidden; // This is a quick fix. Change later
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: auto auto auto auto;
    margin-bottom: 3%;
  }

  #ASUCM {
    overflow: hidden;
    width: 60%;
    left: 60%;
    position: relative;
  }

  #MLH {
    padding-top: 10%;
    width: 50%;
    position: relative;
    left: 10%;
  }

  #Linode {
    width: 70%;
    position: relative;
    top: 20%;
    left: 10%;
