  .events-detail li {
    padding: 10px;
    border-bottom: 1px dashed #ccc;
    line-height: 22px;
    -webkit-transition: ease 0.4s all;
    transition: ease 0.4s all; }
    .events-detail li div {
      text-decoration: none;
      color: white;
      width: 100%;
      height: 100%;
      display: block; }

.event-time {
  font-weight: 900; }

.event-location {
  font-size: 0.8em;
  color: #ffb994;
  margin-left: 70px; }

@media all and (max-width: 641px) {
  .date {
    width: 100%;
    border-bottom: 1px solid #ccc;
    margin-bottom: 10px; }
  .events {
    border: none;
    width: 100%;
    margin-top: 0; }
  .events-detail {
    padding: 0; }
  li.date p {
    margin: 0; } }

#live-map {
  background-color: #9a544c; }
  #live-map #live-parking, #live-map #live-rooms {
    padding: 2%; }

.live-grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  align-items: center;
  grid-gap: 6%;
  height: auto;
  width: auto; }

.live-grid-item {
  text-align: center;
  min-width: 0; }

.container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  font-family: 'Roboto', sans-serif;
  font-size: 100%; }

.card {
  max-width: 400px;
  max-height: 800px;
  margin: 10px;
  text-align: center;
  border: 1px solid #dfe6e9;
  padding-bottom: 35px;
  border-radius: 5px;
  background: #2d3436;
  color: white;
  cursor: default; }

.card img {
  width: 100px;
  margin: 20px 0; }

.card-text {
  padding: 0 15px; }

.divider {
  border: 1px solid #636e72;
  margin: 40px 0; }

.btn {
  padding: 12px 17px;
  text-decoration: none; }

.btn-default {
  color: #fff;
  background: #0984e3;
  border: 2px solid #0984e3;
  border-radius: 5px; }


iframe#instagram-embed-0 {
  max-width: 500px !important;
  height: 700px; }

html {
  --bg-one: #973999;
  --bg-two: #f8598b;
  --bg-three: #f7bf00;
  --bg-panel: #fff;
  --color-headings: #fff;
  --color-text: #333;
  --color-fun: #5bf3f9; }

.fa-stack {
  font-size: 50px;
  text-shadow: 2px 2px 3px rgba(0, 0, 0, 0.3); }
  .fa-stack .fa-stack-1x {
    text-shadow: none;
    color: #5bf3f9;
    color: var(--color-fun); }

#countdown {
  overflow-y: hidden; }

.countdown-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  border-radius: 10px;
  box-shadow: 4px 4px 12px 2px rgba(0, 0, 0, 0.3); }
  @media only screen and (max-width: 1100px) {
    .countdown-wrapper {
      -webkit-transform: none;
              transform: none;
      padding: 30px 0 30px 0; } }

.countdown-item {
  color: #fff;
  color: var(--color-headings);
  font-size: 40px;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  line-height: 30px;
  margin: 10px;
  padding-top: 10px;
  position: relative;
  width: 100px;
  height: 100px; }

.countdown-item span {
  color: #fff;
  color: var(--color-headings);
  font-size: 12px;
  text-transform: uppercase; }

.countdown-svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px; }

#days {
  margin: 10% -75% 0 0; }

#hours {
  margin: 10% -68% 0 0; }

#minutes {
  margin: 10% -60% 0 0; }

#seconds {
  margin: 10% -55% 0 0; }


